/**
 * backends Page
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/home' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/dbs/dbs' }">Dbs</el-breadcrumb-item>
      <el-breadcrumb-item >Table</el-breadcrumb-item>
      <el-breadcrumb-item>Partition</el-breadcrumb-item>
      <el-breadcrumb-item>Index</el-breadcrumb-item>
      <el-breadcrumb-item>Tablet</el-breadcrumb-item>
    </el-breadcrumb>
   <el-form :inline="true" :model="formInline" class="user-search">
   </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;" @row-click="goto_partitions_info">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="TabletId" label="TabletId">
      </el-table-column>
      <el-table-column sortable prop="ReplicaId" label="ReplicaId">
      </el-table-column>
      <el-table-column sortable prop="BackendId" label="BackendId">
      </el-table-column>
      <el-table-column sortable prop="SchemaHash" label="SchemaHash">
      </el-table-column>
      <el-table-column sortable prop="Version" label="Version">
      </el-table-column>
      <el-table-column sortable prop="VersionHash" label="VersionHash">
      </el-table-column>
      <el-table-column sortable prop="LstSuccessVersion" label="LstSuccessVersion">
      </el-table-column>
      <el-table-column sortable prop="LstSuccessVersionHash" label="LstSuccessVersionHash">
      </el-table-column>
      <el-table-column sortable prop="LstFailedVersion" label="LstFailedVersion">
      </el-table-column>
      <el-table-column sortable prop="LstFailedVersionHash" label="LstFailedVersionHash">
      </el-table-column>
      <el-table-column sortable prop="LstFailedTime" label="LstFailedTime">
      </el-table-column>
      <el-table-column sortable prop="DataSize" label="DataSize">
      </el-table-column>
      <el-table-column sortable prop="RowCount" label="RowCount">
      </el-table-column>
      <el-table-column sortable prop="State" label="State">
      </el-table-column>
      <el-table-column sortable prop="LstConsistencyCheckTime" label="LstConsistencyCheckTime">
      </el-table-column>
      <el-table-column sortable prop="CheckVersion" label="CheckVersion">
      </el-table-column>
      <el-table-column sortable prop="CheckVersionHash" label="CheckVersionHash">
      </el-table-column>
      <el-table-column sortable prop="VersionCount" label="VersionCount">
      </el-table-column>
      <el-table-column sortable prop="PathHash" label="PathHash">
      </el-table-column>
       <el-table-column sortable prop="hrefPath" label="hrefPath" v-if="show">
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import { table_info_list} from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      show:false,
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        page: 1,
        limit: 50,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 50,
        total: 100
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    console.log("partitions = "+ self.$route.params.id)
    self = this
  },

  /**
   * 
   */
  methods: {
    goto_partitions_info(row, event, column){
      console.log("hrefPath = "+ row.hrefPath)
      self.$router.push({ 
         path: '/dbs/partitions_tablet_info/' + row.hrefPath.replace(new RegExp(/\//g),'\\')
      })
    },
    // Frontend
    getdata(parameter) {
      this.loading = true    
      table_info_list( self.$route.params.id.replace(new RegExp(/\\/g),'\/'))
        .then(res => {
          this.loading = false
          if (res.success == false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data
            // 
            this.pageparm.currentPage = this.formInline.page
            this.pageparm.pageSize = this.formInline.limit
            this.pageparm.total = res.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
 
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


