<template>
  <div class="login-wrap">
    <el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm login-container">
      <h3 class="title">Apache Doris</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="ruleForm.username" auto-complete="off" placeholder="username"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="ruleForm.password" auto-complete="off" placeholder="password"></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')" :loading="logining">Login</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
import { login } from '../api/userMG'
import axios from 'axios';
import { setCookie, getCookie, delCookie } from '../utils/util'
import md5 from 'js-md5'


export default {
  name: 'login',
  data() {
    return {
      //loadingfalse
      logining: false,
      // 
      rememberpwd: false,
      ruleForm: {
        //usernamepassword
        username: '',
        password: ''
      },
      //rules
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  // ()
  created() {
    this.$message({
      message: '',
      type: 'success'
    })
    self=this
  
  },
  // 
  methods: {
    // 
    getuserpwd() {
      if (getCookie('user') != '' && getCookie('pwd') != '') {
        this.ruleForm.username = getCookie('user')
        this.ruleForm.password = getCookie('pwd')
        this.rememberpwd = true
      }
    },
    //info
    submitForm(formName) {
         axios.post('http://192.168.1.130:8030/rest/v1/login', {
			username: this.ruleForm.username,
			password: this.ruleForm.password
		  })
		  .then(function (response) {	
			if(response.data.code === 200){
			   self.$router.push({ path: '/home/home' })
			} else if(response.data.code === 401){
				self.$router.push({ path: '/login' });
			}
			console.log(response);
		  })
    },
  }
}
</script>

<style scoped>
.login-wrap {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 10%;
  background-image: url(data:image/svg+xml;base64,);
  /* background-color: #112346; */
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 100%;
}
.login-container {
  border-radius: 10px;
  margin: 0px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}
.title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.remember {
  margin: 0px 0px 35px 0px;
}
.code-box {
  text-align: right;
}
.codeimg {
  height: 40px;
}
</style>


