/**
 * backends Page
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/home' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/dbs/dbs' }">Dbs</el-breadcrumb-item>
      <el-breadcrumb-item>Table Info</el-breadcrumb-item>
    </el-breadcrumb>
   <el-form :inline="true" :model="formInline" class="user-search">
   </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;" @row-click="goto_table_info">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="TableId" label="TableId">
      </el-table-column>
      <el-table-column sortable prop="TableName" label="TableName" >
      </el-table-column>
      <el-table-column sortable prop="PartitionColumnName" label="PartitionColumnName" >
      </el-table-column>
       <el-table-column sortable prop="Type" label="Type" >
      </el-table-column>
      <el-table-column sortable prop="State" label="State" >
      </el-table-column>
      <el-table-column sortable prop="ReplicaCount" label="ReplicaCount" >
      </el-table-column>
      <el-table-column sortable prop="IndexNum" label="IndexNum" >
      </el-table-column>
      <el-table-column sortable prop="PartitionNum" label="PartitionNum" >
      </el-table-column>
      <el-table-column sortable prop="LastConsistencyCheckTime" label="LastConsistencyCheckTime" >
      </el-table-column>
       <el-table-column sortable prop="hrefPath" label="hrefPath" v-if="show">
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import { dbs_info_list} from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      show:false,
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 20,
        total: 20
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    self = this
  },

  /**
   * 
   */
  methods: {
    goto_table_info(row, event, column){
      console.log("hrefPath = "+ row.hrefPath)
       self.$router.push({ 
         path: '/dbs/table_info/' + row.hrefPath.replace(new RegExp(/\//g),'\\')
        })
    },
    // Frontend
    getdata(parameter) {
      this.loading = true
      dbs_info_list(self.$route.params.id)
        .then(res => {
          this.loading = false
          if (res.success == false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data
            // 
            this.pageparm.currentPage = this.formInline.page
            this.pageparm.pageSize = this.formInline.limit
            this.pageparm.total = res.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
 
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


