/**
 * backends Page
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/home' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item>Cluster Balance</el-breadcrumb-item>
    </el-breadcrumb>
   <el-form :inline="true" :model="formInline" class="user-search">
   </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;" @row-click="goto_balanceinfo">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="Item" label="Item">
      </el-table-column>
      <el-table-column sortable prop="Number" label="Number" >
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import { cluster_balance_List} from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    self = this
  },

  /**
   * 
   */
  methods: {
    goto_balanceinfo(row, event, column){
      var item = row.Item;  
      var routePath = "" ;
      if(item == "cluster_load_stat"){  
        routePath = "/system/BalanceInfo/"+row.Item.replace(/%/g, '%25') 
      }else if(item == "working_slots"){
        routePath =  "/system/working_slots/"+row.Item.replace(/%/g, '%25')
      } else if(item == "sched_stat"){
          routePath = "/system/sched_stat/"+row.Item.replace(/%/g, '%25')
      } else if(item == "priority_repair"){
          routePath = "/system/priority_repair/"+row.Item.replace(/%/g, '%25')
      } else if(item == "pending_tablets"){
          routePath = "/system/pending_tablets/"+row.Item.replace(/%/g, '%25')
      } else if(item == "running_tablets"){
          routePath = "/system/running_tablets/"+row.Item.replace(/%/g, '%25')
      }else if(item == "history_tablets"){
          routePath = "/system/history_tablets/"+row.Item.replace(/%/g, '%25')
      }
      self.$router.push({ 
          path : routePath
      })
    },
    // Frontend
    getdata(parameter) {
      this.loading = true
      cluster_balance_List(parameter)
        .then(res => {
          this.loading = false
          if (res.success == false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data
            // 
            this.pageparm.currentPage = this.formInline.page
            this.pageparm.pageSize = this.formInline.limit
            this.pageparm.total = res.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
 
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


