/**
 * Jobs Page
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/home' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item :to="{path:'/job/Jobs'}">Jobs DB</el-breadcrumb-item>
      <el-breadcrumb-item>Jobs Operation</el-breadcrumb-item>
      <el-breadcrumb-item>Jobs Rollup</el-breadcrumb-item>
    </el-breadcrumb>
   <el-form :inline="true" :model="formInline" class="user-search">
   </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;" >
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="JobId" label="JobId">
      </el-table-column>
      <el-table-column sortable prop="TableName" label="TableName" >
      </el-table-column> 
      <el-table-column sortable prop="CreateTime" label="CreateTime" >
      </el-table-column> 
      <el-table-column sortable prop="FinishedTime" label="FinishedTime" >
      </el-table-column> 
      <el-table-column sortable prop="BaseIndexName" label="BaseIndexName" >
      </el-table-column> 
      <el-table-column sortable prop="RollupIndexName" label="RollupIndexName" >
      </el-table-column>  
      <el-table-column sortable prop="RollupId" label="RollupId" >
      </el-table-column>  
      <el-table-column sortable prop="TransactionId" label="TransactionId" >
      </el-table-column>  
      <el-table-column sortable prop="State" label="State" >
      </el-table-column>  
      <el-table-column sortable prop="Msg" label="Msg" >
      </el-table-column>  
      <el-table-column sortable prop="Progress" label="Progress" >
      </el-table-column>  
      <el-table-column sortable prop="Timeout" label="Timeout" >
      </el-table-column>  
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import { table_info_list } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
     
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    self = this
  },

  /**
   * 
   */
  methods: {
    // Frontend
    getdata(parameter) {
      this.loading = true
      table_info_list(self.$route.params.id.replace(new RegExp(/\\/g),'\/'))
        .then(res => {
          this.loading = false
          if (res.success == false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data
            // 
            this.pageparm.currentPage = this.formInline.page
            this.pageparm.pageSize = this.formInline.limit
            this.pageparm.total = res.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


