/**
 * backends Page
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">Home</el-breadcrumb-item>
      <el-breadcrumb-item>Backends</el-breadcrumb-item>
    </el-breadcrumb>
   <el-form :inline="true" :model="formInline" class="user-search">
   </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;" @row-click="goto_backendinfo">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="BackendId" label="BackendId">
      </el-table-column>
      <el-table-column sortable prop="Cluster" label="Cluster" >
      </el-table-column>
      <el-table-column sortable prop="DataUsedCapacity" label="DataUsedCapacity" >
      </el-table-column>
       <el-table-column sortable prop="Ip" label="Ip" >
      </el-table-column>
      <el-table-column sortable prop="HostName" label="ClusterId" >
      </el-table-column>
      <el-table-column sortable prop="HeartbeatPort" label="HeartbeatPort" >
      </el-table-column>
      <el-table-column sortable prop="HttpPort" label="HttpPort" >
      </el-table-column>
      <el-table-column sortable prop="Alive" label="Alive" >
      </el-table-column>
      <el-table-column sortable prop="BrpcPort" label="BrpcPort" >
      </el-table-column>
      <el-table-column sortable prop="BePort" label="BePort" >
      </el-table-column>
      <el-table-column sortable prop="AvailCapacity" label="AvailCapacity" >
      </el-table-column>
      <el-table-column sortable prop="TabletNum" label="TabletNum" >
      </el-table-column>
      <el-table-column sortable prop="TotalCapacity" label="TotalCapacity" >
      </el-table-column>
      <el-table-column sortable prop="ClusterDecommissioned" label="ClusterDecommissioned" >
      </el-table-column>
      <el-table-column sortable prop="SystemDecommissioned" label="SystemDecommissioned" >
      </el-table-column>
      <el-table-column sortable prop="LastHeartbeat" label="LastHeartbeat" >
      </el-table-column>
      <el-table-column sortable prop="LastStartTime" label="LastStartTime" >
      </el-table-column>
      <el-table-column sortable prop="UsedPct" label="UsedPct" >
      </el-table-column>
      <el-table-column sortable prop="MaxDiskUsedPct" label="MaxDiskUsedPct" >
      </el-table-column>
      <el-table-column sortable prop="ErrMsg" label="ErrMsg" >
      </el-table-column>
      <el-table-column sortable prop="Version" label="Version" >
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import { backendsList} from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    self = this
  },

  /**
   * 
   */
  methods: {
    goto_backendinfo(row, event, column){
      console.log("BackendId = "+ row.BackendId)
       self.$router.push({ 
         path: '/system/BackendsInfo/'+row.BackendId.replace(/%/g, '%25') 
        })
    },
    // Frontend
    getdata(parameter) {
      this.loading = true
      backendsList(parameter)
        .then(res => {
          this.loading = false
          if (res.success == false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data
            // 
            this.pageparm.currentPage = this.formInline.page
            this.pageparm.pageSize = this.formInline.limit
            this.pageparm.total = res.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
 
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


